import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components:{
		wPicker
	},
	data() {
		return {
			value: [0, 0, 0],
			iconHidden: true,
			showPickerStatus: false,
			searchKey: "",
			real_name:'', //收货人姓名
			is_default:1,//1默认，0不默认
			phone:'',//收货人电话
			detail:'',//收货人详细地址
			resultInfo:'',//三级联动
			
			linkList:[{
				label:"浙江省",
				value:"10",
				children:[{
					label:"杭州市",
					value:"1010",
					children:[{
						label:"滨江区",
						value:"10101"
					},{
						label:"萧山区",
						value:"10102"
					}]
				},{
					label:"宁波市",
					value:"1012",
					children:[{
						label:"海曙区",
						value:"10121"
					},{
						label:"鄞州区",
						value:"10122"
					}]
				}]
			},{
				label:"广东省",
				value:"20",
				children:[{
					label:"广州市",
					value:"2010",
					children:[{
						label:"广州1区",
						value:"20101"
					},{
						label:"广州2区",
						value:"20102"
					}]
				},{
					label:"深圳市",
					value:"2012",
					children:[{
						label:"深圳1区",
						value:"20121"
					},{
						label:"深圳2区",
						value:"20122"
					}]
				}]
			}]
		}
	},
	onNavigationBarButtonTap(){
		this.saveClick()
	},
	onBackPress() {
		
	},
	onLoad(){
		
	},
	methods: {
		//打开三级联动
		openonConfirmClick(){
			this.$refs.picker.show();
		},
		//三级联动
		onConfirm(val){
			console.log(val);
			this.resultInfo = val.result;
		},
		// 保存按钮
		saveClick(){
			if(!this.real_name){
				uni.showToast({
					title:'请输入收货人姓名',
					icon:'none'
				})
			}else if(!(/^1[345789]\d{9}$/.test(this.phone))){
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
			}else if (!this.detail){
				uni.showToast({
					title:'请输入详细地址',
					icon:'none'
				})
			}else{
				let data = {
					address:this.address,
					is_default:this.is_default,
					real_name:this.real_name,
					phone:this.phone,
					detail:this.detail,
					id:this.id,
					token:this.userMessage.token
				}
				edit_user_address(data)
			}
		},
	}
}
